<template>
    <div class="song-list-album">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(songList1.pic)">
            </div>
            <div class="upload-img">
                <template >
                    <el-upload :action="uploadUrl(songList1.id)" :before-upload="beforeAvatorUpload" 
                        :on-success="handleAvatorSuccess">
                        <el-button :style="{display : showUpload}" size="mini"></el-button>
                    </el-upload>
                </template>
            </div>
            <div class="album-info">
                <h2></h2>
                <span>
                    {{songList1.introduction}}
                </span>
            </div>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p>{{songList1.title}}</p>
            </div>
            <span>By{{this.consumerName}}</span>
            <div class="album-score">
                <div>
                    <h3></h3>
                    <div>
                        <el-rate v-model="average" disabled></el-rate>
                    </div>
                </div>
                <span>{{average*2}}</span>
                <div>
                    <h3></h3>
                    <div @click="setSongListRank">
                        <el-rate v-model="rank" allow-half show-text></el-rate>
                    </div>
                </div>
                
            </div>
            <div class="songs-body">
                <album-content :songList="listOfSongs">
                    <template slot="title"></template>
                </album-content>
                <div class="add">
                    <el-button :style="{display : showUpload}" type="primary" @click="handleAdd(songList1)"></el-button>
                    <el-button :style="{display : showUpload}" type="primary" @click="handleEdit(songList1)"></el-button>
                    <el-button :style="{display : showUpload}" type="danger" @click="handleDelete(songList1)"></el-button> 
                </div>
                <comment :playId="songListId" :type="1"></comment>
            </div>
        </div>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="400px" center>
            <el-form :model="registerForm" ref="registerForm" label-width="80px" action="" id="tf">
                <el-form-item prop="singerName" label="" size="mini">
                    <el-input v-model="registerForm.singerName" placeholder=""></el-input>
                </el-form-item> 
                <el-form-item prop="songName" label="" size="mini">
                    <el-input v-model="registerForm.songName" placeholder=""></el-input>
                </el-form-item>  
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible = false"></el-button>
                <el-button size="mini" @click="getAddSongId"></el-button>                
            </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="editVisible" width="400px" center>
            <el-form :model="form" ref="form" label-width="80px">
                <el-form-item prop="title" label="" size="mini">
                    <el-input v-model="form.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="form.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>    
                <el-form-item prop="style" label="" size="mini">
                    <el-input v-model="form.style" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="editVisible = false"></el-button>
                <el-button size="mini" @click="editSave"></el-button>                
            </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="delVisible" width="300px" center>
            <div align="center"></div>
            <span slot="footer">
                <el-button size="mini" @click="delVisible = false"></el-button>
                <el-button size="mini" @click="deleteRow(songList1.id)"></el-button>                
            </span>
        </el-dialog>
    </div>
</template>

<script>
import AlbumContent from '../components/AlbumContent';
import {mixin} from '../mixins/index';
import {mapGetters} from 'vuex';
import {listSongOfSongListId, songOfId, songLikeName, songListOfId, setRank, getRank, getRankNum, setListSong, updateSongList, consumerOfId} from '../api/index';
import {delSongList} from '../api/index';
import Comment from '../components/Comment';
export default {
    name: 'song-list-album',
    inject:['reload'],
    mixins: [mixin],
    components:{
        AlbumContent,
        Comment
    },
    data() {
        return {
            songLists: [], //
            songListId: '',//id
            songList1: [],
            consumerName: '',//
            showUpload: 'none',
            average: 0, //
            rank: 0,//
            centerDialogVisible: false, //
            editVisible: false,         //
            delVisible: false,          //
            registerForm:{      //
                singerName: '',     //
                songName: ''        //
            },
            form:{      //
                id: '',
                title: '',
                introduction: '',
                style: ''
            },
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs', //
            'tempList',//
            'loginIn',//
            'userId',//id
        ])
    },
    created(){
        this.songListId = this.$route.params.id;
        this.getSongId();
        this.songList(this.songListId);
        this.getSongListRank(this.songListId)
        // this.setSongListRank();
    },
    methods:{
        //
        getSongId(){
            listSongOfSongListId(this.songListId)
                .then(res => {
                    this.$store.commit('setListOfSongs',res.data);
                    // console.log(this.listOfSongs)
                }).catch(err =>{
                    console.log(err);
                })
        },
        //id
        getSongList(id){
            songOfId(id).then(res => {
                this.songLists.push(res.data);
            }).catch(err => {
                console.log(err);
            })
        },
        //,
        songList(id){
            songListOfId(id).then( res =>{
                res = res.data;
                this.songList1 = res;
                this.getConsumer(res.userId);
                this.handleUpload(res);
            }).catch(err => {
                console.log(err)
            })
        },
        //
        getConsumer(id){
            if(id){
                consumerOfId(id).then(res => {
                this.consumerName = res.data.username;
                }).catch(err => {
                    console.log(err);
                })
            }else{
                this.consumerName = 'DHU';
            } 
        },
        //
        getSongListRank(id){
            getRank(id).then( res => {
                this.average = res/2;
            }).catch(err => {
                console.log(err);
            })
        },
        //
        setSongListRank(){
            if(this.loginIn){
                let params = new URLSearchParams();
                params.append('songListId',this.songListId);
                params.append('consumerId',this.userId);
                params.append('score',this.rank * 2);
                console.log(params)
                setRank(params).then(res => {
                    if(res.code == 1){
                        this.notify(res.msg,'success');
                        this.getSongListRank(this.songListId);
                    }else{
                        this.notify(res.msg,'error');
                    }
                }).catch(err => {
                        this.notify('','error');
                        console.log(err);
                })
            }else{
                this.rank = null;
                this.notify('','warning');
            }
        },
        //id        
        getAddSongId(){
            let _this = this;
            var songOfName = _this.registerForm.songName+"-"+_this.registerForm.singerName;
            songLikeName(songOfName).then(
                res => {
                    res = res.data;
                    _this.addSong(res[0].id);
                    _this.reload();
                }
            )
        },
        //
        addSong(songId){
            let _this = this;
            let params = new URLSearchParams();
            params.append('songId',songId);
            params.append('songListId',this.songListId);

            setListSong(params)
            .then(res => {
                if(res.success == true){
                    this.songList();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            _this.centerDialogVisible = false;
        },
        //
        handleAdd(row){
            if(row.userId == this.userId){
                this.centerDialogVisible = true;
            }else{
                this.notify('','warning');
            }
        },
        //
        handleEdit(row){
            if(row.userId == this.userId){
                this.editVisible = true;
            this.form = {
                id: row.id,
                title: row.title,
                introduction: row.introduction,
                style: row.style
            }
            }else{
                this.notify('','warning');
            }
        },
        //
        handleUpload(row){
            if(row.userId == this.userId){
                this.showUpload = '';
            }else{
                this.showUpload = 'none';
            }
        },
        //
        handleDelete(row){
            if(row.userId == this.userId){
                this.delVisible = true;
            }else{
                this.notify('','warning');
            }
        },
        //
        editSave(){
            let params = new URLSearchParams();
            params.append('id',this.form.id);
            params.append('title',this.form.title);
            params.append('introduction',this.form.introduction);
            params.append('style',this.form.style);
            updateSongList(params)
            .then(res => {
                if(res.code == 1){
                    // this.songList();
                    this.notify("","success");
                    this.reload();
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.editVisible = false;
        },
        //
        uploadUrl(id){
            // return `http://10.206.154.159:9527/songList/updateSongListPic?id=${id}`
            return `${"http://www.huole.xyz:9527"}/songList/updateSongListPic?id=${id}`
        },
        //
        beforeAvatorUpload(file){
            const isJPG = (file.type === 'image/jpeg')||(file.type === 'image/png');
            if(!isJPG){
                this.$message.error('jpgpng');
                return false;
            }
            const isLt2M = (file.size / 1024 /1024) < 2;
            if(!isLt2M){
                this.$message.error('2MB');
                return false;
            }
            return true;
        },
        //
        handleAvatorSuccess(res){
            let _this = this;
            if(res.code == 1){
                // _this.getData();
                _this.$notify({
                    title: '',
                    type: 'success'
                });
                this.reload();
            }else{
                _this.$notify({
                    title: '',
                    type: 'error'
                });
            }
        },
         //
        deleteRow(id){
            delSongList(id)
            .then(res => {
                if(res){
                    // this.getData();
                    this.notify("","success");
                    this.$router.push({path:'/my-list'});
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.delVisible = false;
        },
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/song-list-album.scss';
</style>


