<template>
    <div class="song-list">
        <ul class="song-list-header">
            <li v-for="(item,index) in songStyle" :key="index" @click="handleChangeView(item.name)" 
            :class="{active:item.name == activeName}">
                {{item.name}}
            </li>
        </ul>
        <div>
            <content-list :contentList="data"></content-list>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                :current-page="curPage" :page-size="pageSize" :total="totalNum"></el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import ContentList from '../components/ContentList';
import {getSongListByPager, likeStyle} from '../api/index';
import {mixin} from '../mixins/index';
import {songStyle} from '../assets/data/songList';
import {mapGetters} from 'vuex';
export default {
    name: 'song-list',
    mixins: [mixin],
    components:{
        ContentList,
    },
    data(){
        return{
            albumDatas: [],//
            pageSize: 10, //
            currentPage: 1, //
            songStyle: [], //
            activeName: '', //
            totalNum: 0,//
        }
    },
    computed:{
        //
        data(){
            return this.albumDatas;
        },
        ...mapGetters([
            'curPage',//
            'allSongLists',//
        ])
    },
    mounted(){
        this.songStyle = songStyle;
        this.getSongList();
        this.activeName = '';
    },
    methods:{
        getSongList(){
            getSongListByPager(this.curPage, this.pageSize)
            .then( res => {
                // this.$store.commit('setCurPage',1);
                this.albumDatas = res.data.rows;
                this.totalNum = res.data.total;
                this.$store.commit('setAllSongLists',res);
            }).catch(err => {
                console.log(err);
                this.notify("","warning");    
            })
        },
        //
        handleCurrentChange(val){
            // this.curPage = val;
            this.$store.commit('setCurPage',val);
            if(this.activeName == ''){
                getSongListByPager(this.curPage, this.pageSize)
                .then( res => {
                    this.$store.commit('setCurPage',1);
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                    this.$store.commit('setAllSongLists',res);
                }).catch(err => {
                    console.log(err);
                    this.notify("","warning");    
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == 'BGM'){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == 'R&B'){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ''){
                likeStyle(this.activeName, this.curPage, this.pageSize)
                .then( res => {
                    this.albumDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            
        },
        //style
        handleChangeView(name){
            this.activeName = name;
            this.albumDatas = [];
            if(name == ''){
                this.getSongList();
            }else{
                this.goSongListOfStyle(name);
            }
        },
        //style
        goSongListOfStyle(style){
            likeStyle(style, this.curPage, this.pageSize)
            .then( res => {
                // this.$store.commit('setCurPage',1);
                this.albumDatas = res.data.rows;
                this.totalNum = res.data.total;
            }).catch( err => {
                console.log(err)
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/song-list.scss';
</style>


