<template>
    <div class="song-list">
        <ul class="song-list-header">
            <li v-for="(item,index) in songStyle" :key="index" @click="handleChangeView(item.name)" 
            :class="{active:item.name == activeName}">
                {{item.name}}
            </li>
            <el-button type="primary"  @click="centerDialogVisible = true"></el-button>
        </ul>
        <div>
            <my-content-list :myContentList="data"></my-content-list>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                :current-page="curPage" :page-size="pageSize" :total="albumDatas.length"></el-pagination>
            </div>
        </div>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="400px" center>
            <el-form :model="registerForm" ref="registerForm" label-width="80px">
                <el-form-item prop="title" label="" size="mini">
                    <el-input v-model="registerForm.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="registerForm.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>
                <el-form-item prop="style" label="" size="mini">
                    <el-input v-model="registerForm.style" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible = false"></el-button>
                <el-button size="mini" @click="addSongList"></el-button>                
            </span>
        </el-dialog>
    </div>
</template>
<script>
import MyContentList from '../components/MyContentList';
import {getAllConsumerSongList, getAllSongList, likeStyle, likeTitle, setConsumerSongList} from '../api/index';
import {mixin} from '../mixins/index';
// import {songStyle} from '../assets/data/songList';
import {mapGetters} from 'vuex';
export default {
    name: 'song-list',
    mixins: [mixin],
    inject:['reload'],
    components:{
        MyContentList,
    },
    data(){
        return{
            albumDatas: [],//
            pageSize: 10, //
            currentPage: 1, //
            songStyle: [
                {name:''},
            ], //
            activeName: '', //

            centerDialogVisible: false, //
            editVisible: false,         //
            delVisible: false,          //
            registerForm:{      //
                title: '',
                introduction: '',
                style: ''
            },
            form:{      //
                id: '',
                title: '',
                introduction: '',
                style: ''
            },
            tableData: [],
            tempData: [],
            select_word: '',
            pageSize: 5,    //
            currentPage: 1,  //
            idx: -1,          //
            multipleSelection: []   //
        }
    },
    computed:{
        //
        data(){
            return this.albumDatas.slice((this.curPage - 1) * this.pageSize,this.curPage * this.pageSize)
        },
        ...mapGetters([
            'curPage',//
            'userId',//id
        ])
    },
    mounted(){
        
        this.getSongList(this.userId);
        this.activeName = '';
    },
    methods:{
        getSongList(id){
            getAllConsumerSongList(id)
                .then( res => {
                    this.$store.commit('setCurPage',1);
                    this.albumDatas = res.data;
                    // console.log(res)
                }).catch(err => {
                    console.log(err);
                    this.notify("","warning");    
                })
        },
        //
        handleCurrentChange(val){
            // this.curPage = val;
            this.$store.commit('setCurPage',val);
        },
        //style
        handleChangeView(name){
            this.activeName = name;
            this.albumDatas = [];
            if(name == ''){
                this.getSongList();
            }else{
                this.goSongListOfStyle(name);
            }
        },
        //style
        goSongListOfStyle(style){
            likeStyle(style).then( res => {
                this.$store.commit('setCurPage',1);
                this.albumDatas = res.data;
            }).catch( err => {
                console.log(err)
            })
        },
        //
        addSongList(){
            let params = new URLSearchParams();
            params.append('title',this.registerForm.title);
            params.append('pic','/img/songListPic/list.jpg');
            params.append('introduction',this.registerForm.introduction);
            params.append('style',this.registerForm.style);
            params.append('userId',this.userId)
            setConsumerSongList(params)
            .then(res => {
                if(res.code == 1){
                    // this.getData();
                    this.notify("","success");
                    this.reload();
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.centerDialogVisible = false;
        },
    }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/song-list.scss';
</style>


