<template>
    <div class="login-logo">
        <svg class="logo">
            <use xlink:href="#icon-ruererji"></use>
        </svg>
    </div>
</template>
<script>

import {rules, cities} from '../assets/data/form'
export default {
    name: 'login-logo',

    data() {
        return {
            registerForm: {
                username: '',  //
                password: '', //
                sex:'',//
                phoneNum: '',//
                email: '',//
                birth:'',//
                introduction: '',//
                location:'',//
            },
            cities: [],//--
            rules: {},//
        }
    },
    created(){
        this.rules = rules;
        this.cities = cities;
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/login-logo.scss';
</style>


