<template>
<div class="bk">
    <!-- <loginLogo/> -->
    <div class="login">
        <div class="login-head">
            <span></span>
        </div>
        <el-form :model="loginForm" ref="loginForm" label-width="70px" class="demo-ruleForm" :rules="rules">
            <el-form-item prop="username" label="">
                <el-input v-model="loginForm.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="password" label="">
                <el-input type="password" v-model="loginForm.password" placeholder=""></el-input>
            </el-form-item>
            <div class="login-btn">
                <el-button @click="goSignUp"></el-button>
                <el-button type="primary" @click="handleLoginIn"></el-button>
            </div>
        </el-form>
    </div>
</div>
    
</template>

<script>
import {rules, cities} from '../assets/data/form'
import loginLogo from '../components/LoginLogo'
import {verifyConsumer,loginIn} from '../api/index'
import {mixin} from '../mixins/index'
import md5 from 'js-md5'
export default {
    name: 'login-in',
    mixins: [mixin],
    components: {
        loginLogo
    },
    data() {
        return {
            loginForm: {
                username: '',  //
                password: '', //
                sex: '',//
                phoneNum: '',//
                email: '',//
                birth:'',//
                introduction: '',//
                location:'',//
            },
            cities: [],//--
            rules: {},//
        }
    },
    created(){
        this.rules = rules;
        this.cities = cities;
    },
    mounted(){
        this.changeIndex('')
    },
    methods:{
        handleLoginIn(){
            let _this = this;
            let params = new URLSearchParams();
            params.append('username',this.loginForm.username);
            params.append('password',md5(this.loginForm.password));
            loginIn(params)
                .then(res => {
                    if(res.code == 1){
                        _this.notify('','success');
                        _this.$store.commit('setLoginIn',true);
                        _this.$store.commit('setUserId',res.userMsg[0].id);
                        _this.$store.commit('setUsername',res.userMsg[0].username);
                        _this.$store.commit('setAvator',res.userMsg[0].avator);
                        // console.log(res.userMsg[0].avator)
                        setTimeout(function(){
                            _this.changeIndex('');
                            _this.$router.push({path: '/'})
                        },2000)
                    }else{
                        _this.notify('','error');
                    }
                })
                .catch(err => {
                    _this.notify('','error');
                })
        },
        goSignUp(){
            this.changeIndex('');
            this.$router.push({path: '/sign-up'});
        },
        changeIndex(value){
            this.$store.commit('setActiveName',value);
        }
    }
}
</script>
<style lang="scss" scoped>
// @import '../assets/css/login-in.scss';
.bk{
    background: url('../assets/img/bk.jpeg');
    overflow: auto;
    padding-bottom: 60%;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}

.login {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 300px;
  height: 160px;
  margin-left: -190px;
  margin-top: -150px;
  padding: 40px;
  border-radius: 5px;
  /* background: #fff; */
  background: rgba(255,255,255,0.5);
  color: rgba(255,255,255,1);
  

  .login-head {
    text-align: center;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 600;
  }
  .login-btn {
      padding-left: 70px;
      padding-bottom: 100%;
      margin-top: -10px;
      position: relative;
  }
}

</style>


