<template>
    <div class="song-audio">
        <audio ref="player" 
        :src="url"
        controls = "controls"
        preload="true"
        @canplay="startPlay"
        @ended="ended"
        @timeupdate="timeupdate"></audio>
    </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
    name: 'song-audio',
    computed:{
        ...mapGetters([
            'id',//id
            'url',//
            'isPlay',//
            'listOfSongs', //
            'playButtonUrl', //
            'duration',//
            'curTime',//
            'changeTime', //
            'title', //
            'artist', //
            'picUrl', //
            'autoNext', //
            'lyric', //
            'tempList', //
            'listIndex', //
            'volume',  //50
        ])
    },
    watch:{
        //
        isPlay(){
            this.togglePlay();
        },
        //
        changeTime(){
            this.$refs.player.currentTime = this.changeTime;
        },
        //
        volume(val){
            this.$refs.player.volume = val;
        }
    },
    methods:{
        //
        startPlay(){
            let player = this.$refs.player;
            this.$store.commit('setDuration',player.duration);
            //
            player.play();
            this.$store.commit('setIsPlay',true);
        },
        //
        ended(){
            this.$store.commit('setIsPlay',false);
            this.$store.commit('setCurTime',false);
            this.$store.commit('setAutoNext',!this.autoNext)
        },
        //
        togglePlay(){
            let player = this.$refs.player;
            if(this.isPlay){
                player.play();
            }else{
                player.pause();
            }
        },
        //
        timeupdate(){
            this.$store.commit('setCurTime',this.$refs.player.currentTime);
        }
    }
}
</script>

<style scoped>
    .song-audio {
        display: none;
    }
</style>


