<template>
    <div class="singer-album">
        <div class="album-slide">
            <div class="singer-img">
                <img :src="attachImageUrl(singer.pic)" alt="">
            </div>
            <ul class="info">
                <li v-if="singer.sex == 0 || singer.sex == 1">{{attach(singer.sex)}}</li>
                <li>{{attachBirth(singer.birth)}}</li>
                <li>{{singer.location}}</li>
            </ul>
        </div>
        <div class="album-content">
            <div class="intro">
                <h2>{{singer.name}}</h2>
                <span>{{singer.introduction}}</span>
            </div>
            <div class="content">
            <album-content :songList="data">
                <template slot="title"></template>
            </album-content>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                :current-page="currentPage" :page-size="pageSize" :total="totalNum"></el-pagination>
            </div>
        </div>
        </div>
        
    </div>
</template>

<script>
import AlbumContent from '../components/AlbumContent';
import {mixin} from '../mixins/index';
import {mapGetters} from 'vuex';
import {songOfSingerId} from '../api/index'
import Comment from '../components/Comment';
export default {
    name: 'singer-album',
    mixins: [mixin],
    components:{
        AlbumContent,
        Comment
    },
    data() {
        return {
            singer: {}, //
            singerId: '',//id
            songList1: [],
            pageSize: 10, //
            currentPage: 1, //
            totalNum: 0, //
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs', //
            'tempList',//
            'loginIn',//
            'userId',//id
        ]),
        //
        data(){
            return this.listOfSongs;
        }
    },
    created(){
        this.singerId = this.$route.params.id;
        this.singer = this.tempList;
        this.getSong();
        

    },
    methods:{
        //id
        getSong(){
            songOfSingerId(this.singerId, this.currentPage, this.pageSize)
            .then(res => {
                this.$store.commit('setListOfSongs',res.data.rows);
                this.totalNum = res.data.total;
            }).catch(err =>{
                console.log(err);
            })
        },
        //
        attach(value){
            if(value==0){
                return '';
            }else if(value == 1){
                return '';
            }else{
                return "";
            }
        },
        //
        handleCurrentChange(val){
            this.currentPage = val;
            songOfSingerId(this.singerId, this.currentPage, this.pageSize)
            .then(res => {
                this.$store.commit('setListOfSongs',res.data.rows);
                this.totalNum = res.data.total;
            }).catch(err =>{
                console.log(err);
            })
        },
    } 
}
</script>

<style lang="scss" scoped>
@import '../assets/css/singer-album.scss';
</style>


