<template>
    <transition name="slide-fade">
        <div class="the-aside" v-if="showSongList">
            <h2 class="title"></h2>
            <ul class="menus">
                <li v-for="(item,index) in userSongList" :key="index" :class="{'is-play': id==item.id}"
                @click="addToSongList(id,item.id)">
                    {{item.title}}
                    <!-- {{index}} -->
                </li>
            </ul>
        </div>
    </transition>
</template>
<script>
import {mapGetters} from 'vuex';
import {download, setCollect, getCollectOfUserId, setListSong, getAllConsumerSongList} from '../api/index';
export default {
    name: 'the-user-song-list',
    inject:['reload'],
    data() {
        return {
            userSongList: [],
        }
    },
    computed:{
        ...mapGetters([
            'showSongList', //
            'id',//id
            'loginIn',//
            'userId',//id
            'isActive',//
        ])
    },
    mounted(){
        let _this = this;
        _this.getUserSongList(this.userId);
        document.addEventListener('click',function(){
            _this.$store.commit('setShowSongList',false)
        },true);
    },
    methods: {
        //
        notify(title, type){
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        getUserSongList(userId){
            if(this.loginIn){
                getAllConsumerSongList(userId)
                .then( res => {
                // console.log(userId)
                    if(res.data){
                        this.userSongList = res.data;
                    }
                    else{
                        this.notify("","warning");
                    }
                }).catch(err => {
                    console.log(err);
                    this.notify("","error");    
                })
            }
        },
        addToSongList(songId,songListId){
            let _this = this;
            let params = new URLSearchParams();
            params.append('songId',songId);
            params.append('songListId',songListId);
            setListSong(params)
            .then(res => {
                if(res.success == true){
                    this.reload();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
        }
    }
}
</script>


<style  lang="scss" scoped>
@import '../assets/css/the-aside.scss';
</style>



