const rules = {
  username: [
    {message: '', required: true, trigger: 'blur' }
  ],
  password: [
    {message: '6-10(+)+-', required: true,pattern: /^[A-Z]([A-Z]|[a-z]|[1-9]|[+-]){5,9}/, trigger: 'blur' }
  ],
  sex: [
    { required: true, message: '', trigger: 'change' }
  ],
  phoneNum: [
    { message: '',pattern: /^1[3456789]\d{9}$/, trigger: 'blur' }
  ],
  email: [
    { message: '', trigger: 'blur' },
    { type: 'email', message: '', trigger: ['blur', 'change'] }
  ],
  birth: [
    { required: true,  message: '', trigger: 'change' }
  ],
  introduction: [
    { message: '', trigger: 'blur' }
  ],
  location: [
    { message: '', trigger: 'change' }
  ]
}

