<template>
    <div class="player-container">
    <video-player class="vjs-custom-skin" :options="playerOptions"></video-player>
    </div>
</template>
<script>
 //video
import 'video.js/dist/video-js.css'
import 'vue-video-player/src/custom-theme.css'
  //hls.js
import videojs from 'videojs-contrib-hls'
import { songOfId } from '../api/index';

  export default {
    data () {
      return {
        songId: '',
        playerOptions: {
          playbackRates: [0.7, 1.0, 1.5, 2.0], //
          autoplay: false, //true,
          controls: true, //
          preload: 'auto', //
          muted: false, //
          loop: false, //
          language: 'zh-CN',
          aspectRatio: '16:9', //  - "16:9""4:3"
          fluid: true, // trueVideo.js player
          sources: [{
            type: 'video/mp4',
            src: ''//
          }],
          poster: "http://www.huole.xyz:9527/img/songListPic/1608282201302.jpg", //
          width: document.documentElement.clientWidth,
          notSupportedMessage: '' //Video.js
        }
      }
    },
    created(){
        this.songId = this.$route.params.id;
        this.getUrl(this.songId);
    },
    methods:{
        getUrl(id){
            // console.log(this.playerOptions.sources[0].src)
            songOfId(id).then(res => {
              res = res.data;
                if(res.mv != null){
                    this.playerOptions.sources[0].src = res.mv;
                    // console.log(this.playerOptions.sources[0].src)
                }
                else{
                    this.playerOptions.sources[0].src = 'https://mvwebfs.yun.kugou.com/202101181845/950767654d6b8db02f59ea46ef5b62a8/G214/M09/10/10/docBAF-lR_SAZZZyBoxdFPp8vVM848.mp4';
                }
            })
        }
    }
  }
</script>

<style scoped>
.player-container {
    position: relative;
    margin-top: 100px;
    margin-left: 280px;
    height: 100%;
    width: 70%;
}
</style>


