<template>
    <div class="my-music">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(avator)" alt="">
            </div>
           <ul class="album-info">
                <li>{{username}}</li>
                <li> {{userSex}}</li>
                <li>{{birth}}</li>
                <li>{{location}}</li>
                
            </ul>
        </div>
        <div class="album-content">
            <div class="album-title">
                <li>{{introduction}}</li>
            </div>
            <div class="songs-body">
                <album-content :songList = "collectList">
                <template slot="title"></template>
            </album-content>
        </div>
        </div>
        
    </div>
</template>

<script>
import {mixin} from '../mixins/index';
import {mapGetters} from 'vuex';
import AlbumContent from '../components/AlbumContent';
import {consumerOfId, getCollectOfUserId, songOfId} from '../api/index';
export default {
    name: 'my-music',
    mixins: [mixin],
    components:{
        AlbumContent
    },
    data(){
        return {
            avator: '',//
            username: '',//
            userSex: '',//
            birth: '',//
            location: '',//
            introduction: '',//
            collection: [],//
            collectList: [],//
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',//
            'userId',//id
        ])
    },
    mounted(){
        this.getMsg(this.userId);
        this.getCollection(this.userId);
    },
    methods:{
        getMsg(userId){
            consumerOfId(userId).then( res => {
                res = res.data;
                this.avator = res.avator;
                this.username = res.username;
                if(res.ssex == 0){
                    this.userSex = '';
                }else if(res.sex == 1){
                    this.userSex = '';
                }else{
                    this.userSex = '';
                }
                this.birth = this.attachBirth(res.birth);
                this.location = res.location;
                this.introduction = res.introduction;
            }).catch(err => {
                    console.log(err);
            })
        },
        //
        getCollection(userId){
            getCollectOfUserId(userId).then( res => {
                this.collection = res;
                //id
                for(let item of this.collection){
                   this.collectList.push(item);
                }
            }).catch(err => {
                    console.log(err);
            })
            this.$store.commit('setListOfSongs',this.collectList);
        },
        //id
        // getSongOfIds(id){
        //     songOfId(id)
        //         .then( res => {
        //             this.collectList.push(res.data);
        //         })
        //         .catch(err => {
        //             console.log(err);
        //         })
        //         this.$store.commit('setListOfSongs',this.collectList);
        // }
    }
    
}
</script>
<style lang="scss" scoped>
@import '../assets/css/my-music.scss'
</style>


