<template>
    <div class="singer">
        <ul class="singer-header">
            <li v-for="(item,index) in singerStyle" :key="index" @click="handleChangeView(item)" 
            :class="{active:item.name == activeName}">
                {{item.name}}
            </li>
        </ul>
        <div>
            <content-list :contentList="data"></content-list>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                :current-page="curPage" :page-size="pageSize" :total="totalNum"></el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import ContentList from '../components/ContentList';
import {getAllSinger,getSingerByPager,getSingerOfSex} from '../api/index';
import {mixin} from '../mixins/index';
import {singerStyle} from '../assets/data/singer';
import {mapGetters} from 'vuex';
export default {
    name: 'song-list',
    mixins: [mixin],
    components:{
        ContentList,
    },
    data(){
        return{
            singerDatas: [],//
            pageSize: 10, //
            currentPage: 1, //
            singerStyle: [], //
            activeName: '', //
            totalNum: 0,//
        }
    },
    computed:{
        //
        data(){
            return this.singerDatas;
        },
        ...mapGetters([
            'curPage',//
            'allSingers',//shou
        ])
    },
    mounted(){
        this.singerStyle = singerStyle;
        this.getSinger();
        this.activeName = '';
    },
    methods:{
        getSinger(){
            getSingerByPager(this.curPage, this.pageSize)
            .then( res => {
                this.singerDatas = res.data.rows;
                this.totalNum = res.data.total;
            }).catch( err => {
                console.log(err);
                this.notify("","warning");    
            })
        },
        //
        handleCurrentChange(val){
            // this.curPage = val;
            this.$store.commit('setCurPage',val);
            if(this.activeName == ""){
                getSingerByPager(this.curPage, this.pageSize)
                .then( res => {
                    this.singerDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err);
                    this.notify("","warning");    
                })
            }
            else if(this.activeName == ""){
                getSingerOfSex("1", this.curPage, this.pageSize)
                .then( res => {
                    this.singerDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else if(this.activeName == ""){
                getSingerOfSex("0", this.curPage, this.pageSize)
                .then( res => {
                    this.singerDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
            else{
                getSingerOfSex("2", this.curPage, this.pageSize)
                .then( res => {
                    this.singerDatas = res.data.rows;
                    this.totalNum = res.data.total;
                }).catch( err => {
                    console.log(err)
                })
            }
        },
        //style
        handleChangeView(item){
            this.activeName = item.name;
            this.singerDatas = [];
            if(item.name == ''){
                this.getSinger();
            }else{
                this.goSingerOfSex(item.type);
            }
        },
        //
        goSingerOfSex(sex){
            getSingerOfSex(sex, this.curPage, this.pageSize).then( res => {
                // this.curPage = 1;
                // this.$store.commit('setCurPage',1);
                this.singerDatas = res.data.rows;
                this.totalNum = res.data.total;
            }).catch( err => {
                console.log(err)
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/singer.scss';
</style>


