<template>
    <div class="play-bar" :class="{show:toggle}">
        <div @click="showPlayer(toggle)" class="item-up" :class="{turn: toggle}">
            <svg class="icon">
                <use xlink:href="#icon-jiantou-xia-cuxiantiao"></use>
            </svg>
        </div>
        <div class="kongjian">
            <!--  -->
            <div class="item" @click="prev">
                <svg class="icon">
                    <use xlink:href="#icon-ziyuanldpi"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item" @click="togglePlay">
                <svg class="icon">
                    <use :xlink:href="playButtonUrl"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item" @click="next">
                <svg class="icon">
                    <use xlink:href="#icon-ziyuanldpi1"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item-img" @click="toLyric">
                <img :src="picUrl"/>
            </div>
            <!--  -->
            <div class="playing-speed">
                <!--  -->
                <div class="current-time">{{nowTime}}</div>
                <div class="progress-box">
                    <div class="item-song-title">
                        <div>{{showLessTitle(title)}}</div>
                        <div @click="goSinger(id)">{{this.artist}}</div>
                    </div>
                    <div ref="progress" class="progress" @mousemove="mousemove">
                        <!--  -->
                        <div ref="bg" class="bg" @click="updatemove">
                            <div ref="curProgress" class="cur-progress" :style="{width:curLength+'%'}"></div>
                        </div>
                        <!--  -->
                        <div ref="idot" class="idot" :style="{left:curLength+'%'}" @mousedown="mousedown" @mouseup="mouseup"></div>
                    </div>
                </div>
                <!--  -->
                <div class="left-time">{{songTime}}</div>
                <!--  -->
                <div class="item item-volume">
                    <svg v-if="volume == 0" class="icon">
                        <use xlink:href="#icon-yinliangjingyinheix"></use>
                    </svg>
                    <svg v-else class="icon">
                        <use xlink:href="#icon-yinliang1"></use>
                    </svg>
                    <el-slider class="volume" v-model="volume" :vertical="true"></el-slider>
                </div>
                <!---->
                <div class="item" @click="handleSingle(single)">
                    <svg :class="{single:isSingle}" class="icon">
                        <use xlink:href="#icon-singlePlay"></use>
                    </svg>
                </div>
                <!---->
                <div class="item" @click="handleRandom(random)">
                    <svg :class="{rand:isRandom}" class="icon">
                        <use xlink:href="#icon-randomPlay"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="handleCollect(userId,id)">
                    <svg :class="{active:isActive}" class="icon">
                        <use xlink:href="#icon-xihuan-shi"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="changeSongList">
                    <svg class="icon">
                        <use xlink:href="#icon-shoucang"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="download">
                    <svg class="icon">
                        <use xlink:href="#icon-xiazai"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="changeAside">
                    <svg class="icon">
                        <use xlink:href="#icon-liebiao"></use>
                    </svg>
                </div>
                
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters} from 'vuex';
import {download, setCollect, getCollectOfUserId, delCollectByUserIdSongId, setListSong, songOfId, getSingerOfId} from '../api/index';
export default {
    name: 'play-bar',
    inject:['reload'],
    data(){
        return {
            nowTime: '00:00',        //
            songTime: '00:00',        //
            curLength: 0,            //
            progressLength: 0,      //
            mouseStartX:    0,      //
            tag: false,             //true
            volume: 50,             //
            toggle: false,          //
            singerId: '',
            random: false,          //
            single: false,          //
            listSingleSong: [],
        }
    },
    computed: {
        ...mapGetters([
            'id',               //id
            'url',              //
            'isPlay',           //
            'playButtonUrl',   //
            'picUrl',            //
            'title',                  //
            'artist',                 //
            'duration',                //
            'curTime',                //
            'showAside',            //
            'showSongList',//
            'listIndex', //id
            'listOfSongs',//
            'autoNext',//
            'loginIn',//
            'userId',//id
            'isActive',//
            'isRandom',//
            'isSingle',//
        ])
    },
    watch:{
        //
        isPlay() {
             if(this.isPlay){
                this.$store.commit('setPlayButtonUrl', '#icon-zanting');
            }else{
                this.$store.commit('setPlayButtonUrl', '#icon-bofang');
            }
        },
        curTime(){
            this.nowTime = this.formatSeconds(this.curTime);
            this.songTime = this.formatSeconds(this.duration);
            this.curLength = (this.curTime / this.duration) *100;
        },
        //
        volume(){
            this.$store.commit('setVolume',this.volume / 100);
        },
        //
        autoNext(){
            this.next();
            this.nowTime = '00:01';
            this.togglePlay();
        }
    },
    mounted(){
        this.progressLength = this.$refs.progress.getBoundingClientRect().width;
        document.querySelector('.item-volume').addEventListener('click',function(e){
            document.querySelector('.volume').classList.add('show-volume');
            e.stopPropagation();
        },false);
        document.querySelector('.volume').addEventListener('click',function(e){
            e.stopPropagation();
        },false);
        document.addEventListener('click',function(){
            document.querySelector('.volume').classList.remove('show-volume');
        },false);
    },
    methods: {
        //
        notify(title, type){
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        showLessTitle(title){
            if(title.split("").length>20){
                return title.substr(0,20) + "...";
                // console.log(title.substr(0,20))
            }else{
                return title;
            }
        },
        //
        goSinger(id){
            songOfId(id).then( res => {
                this.singerId = res.singerId;
                getSingerOfId(this.singerId)
                .then(res => {
                    this.$store.commit('setTempList',res);
                    this.$router.push({name:'singer-album',params:{id:this.singerId}});
                }).catch(err => {
                    console.log(err);
                })
            }).catch(err => {
                console.log(err);
            })
        },
        //
        togglePlay() {
            if(this.isPlay){
                this.$store.commit('setIsPlay', false);
            }else{
                this.$store.commit('setIsPlay', true);
            }
        },
        //
        formatSeconds(value){
            let theTime = parseInt(value);
            let result = '';    //
            let hour = parseInt(theTime / 3600);    //
            let minute = parseInt((theTime / 60) % 60); //
            let second = parseInt(theTime % 60);        //
            if(hour > 0){
                if(hour < 10){
                    result = '0' + hour + ":";
                }else{
                    result = hour + ":";
                }
            }
            if(minute > 0){
                if(minute < 10){
                    result += "0" + minute + ":";
                }else{
                    result += minute + ":";
                }
            }else{
                result += "00:";
            }
            if(second > 0){
                if(second < 10){
                    result += "0" + second;
                }else{
                    result += second;
                }
            }else{
                result += "00";
            }
            return result;
        },
        //
        mousedown(e){
            this.mouseStartX = e.clientX;
            this.tag = true;
        },
        //
        mouseup(){
            this.tag = false;
        },
        //
        mousemove(e){
            if(!this.duration){
                return false;
            }
            if(this.tag){
                let movementX = e.clientX - this.mouseStartX;       //
                let curLength = this.$refs.curProgress.getBoundingClientRect().width;
                let newPercent = ((movementX+curLength)/this.progressLength)*100;
                if(newPercent>100){
                    newPercent = 100;
                }
                this.curLength = newPercent;
                this.mouseStartX = e.clientX;
                this.changeTime(newPercent);
            }
        },
        //
        changeTime(percent){
            let newCurTime = (percent*0.01)* this.duration;
            this.$store.commit('setChangeTime',newCurTime);
        },
        //
        updatemove(e){
            if(!this.tag){
                //
                let curLength = this.$refs.bg.offsetLeft;
                let newPercent = ((e.clientX - curLength) / this.progressLength) * 100;
                if(newPercent>100){
                    newPercent = 100;
                }else if(newPercent<0){
                    newPercent = 0;
                }
                this.curLength = newPercent;
                this.changeTime(newPercent);
            }
        },
        //
        changeAside(){
            this.$store.commit('setShowAside',true);
        },

        //
        changeSongList(){
            this.$store.commit('setShowSongList',true);
        },
        //
        prev(){
            if(this.listIndex != -1 && this.listOfSongs.length > 0){//
                if(this.single){
                    this.$store.commit('setListIndex',this.listIndex);//
                }
                else if(this.listIndex > 0){//
                    this.$store.commit('setListIndex',this.listIndex - 1);//
                    
                }else{//
                    this.$store.commit('setListIndex',this.listOfSongs.length - 1);//
                }
                this.toplay(this.listOfSongs[this.listIndex].url);
            }
        },
        //
        next(){
            let len = this.listOfSongs.length;
            if(this.listIndex != -1 && len > 0){//
                if(this.single){
                    this.$store.commit('setListIndex',this.listIndex);//
                }
                else if(!this.random){
                    if(this.listIndex < len - 1){//
                        this.$store.commit('setListIndex',this.listIndex + 1);//
                    
                    }else{//
                        this.$store.commit('setListIndex',0);//
                    }
                }else{
                    let index1 = Math.floor(Math.random(0.1,1) * len);
                    this.$store.commit('setListIndex',index1);//
                }
                this.toplay(this.listOfSongs[this.listIndex].url);
            }
        },
        //
        //
        toplay: function(url){
            if(this.toggle){
                this.showPlayer();
            }
            if(url){
                this.$store.commit('setId',this.listOfSongs[this.listIndex].id);
                this.$store.commit('setUrl',"http://www.huole.xyz:9527"+url);
                this.$store.commit('setPicUrl',"http://www.huole.xyz:9527"+this.listOfSongs[this.listIndex].pic);
                this.$store.commit('setTitle',this.listOfSongs[this.listIndex].name.split('-')[0]);
                this.$store.commit('setArtist',this.listOfSongs[this.listIndex].name.split('-')[1]);
                this.$store.commit('setLyric',this.parseLyric(this.listOfSongs[this.listIndex].lyric));
                this.$store.commit('setIsActive',false);
                if(this.loginIn){
                    getCollectOfUserId(this.userId)
                        .then( res => {
                            for(let item of res){
                                // console.log(item)
                                if(item.songId == this.id){
                                    this.$store.commit('setIsActive',true);
                                    break
                                }
                            }
                        })
                }
            }
        },
        //
        parseLyric(text){
            let lines = text.split("\n\n");   //
            
            if(lines.length < 2){
                lines = text.split("\r\n\r\n");
            }
            if(lines.length < 2){
                lines = text.split("\r\n");
            }
            if(lines.length < 2){
                lines = text.split("\n");
            }
            let pattern = /\[\d{2}:\d{2}.\d{2,3}\]/g;//
            let result = [];
            //
            if(!(/\[.+\]/.test(text))){
                return [[0,text]]
            }
            //
            while(!pattern.test(lines[0])){
                lines = lines.slice(1);
            }
            //+
            for(let item of lines){
                let time = item.match(pattern);//
                let value = item.replace(pattern,'');//
                for(let item1 of time){
                    let t = item1.slice(1,-1).split(":");//
                    if(value!=''){
                        result.push([parseInt(t[0],10) * 60 + parseFloat(t[1]), value]);
                    }
                }
            }
            //
            result.sort(function(a,b){
                return a[0] - b[0];
            });
        },
        //
        toLyric(){
            
            this.$router.push({path:`/lyric`});
            // this.reload();
        },
        //
        download(){
            download(this.url)
            .then(res => {
                let content = res.data;
                let eleLink = document.createElement('a');
                eleLink.download = `${this.title}-${this.artist}.mp3`;
                eleLink.style.display = 'none';
                //blob
                let blob = new Blob([content]);
                eleLink.href = URL.createObjectURL(blob);
                //document
                document.body.appendChild(eleLink);
                //
                eleLink.click();
                //
                document.body.removeChild(eleLink);
            })
            .catch(err => {
                console.log(err);
            })
        },
        //
        handleSingle(val){
            if(val){
                this.single = false;
                this.$store.commit('setIsSingle',false);
                this.notify("","success");
            }else{
                this.single = true;
                this.random = false;
                this.$store.commit('setIsRandom',false);
                this.$store.commit('setIsSingle',true);
                this.notify("",'success');
            }
        },
        //
        handleRandom(val){
            if(val){
                this.random = false;
                this.$store.commit('setIsRandom',false);
                this.notify("","success");
            }else{
                this.random = true;
                this.single = false;
                this.$store.commit('setIsSingle',false);
                this.$store.commit('setIsRandom',true);
                this.notify("",'success');
            }
        },
        //
        handleCollect(userId,songId){
            // console.log(userId)
            if(this.isActive){
                this.delCollect(userId,songId);
                if(this.$route.path == '/my-music'){
                    this.reload();
                }
            }else{
                this.collection();
                if(this.$route.path == '/my-music'){
                    this.reload();
                }
            }
        },
        //
        collection(){
            if(this.loginIn){
                var params = new URLSearchParams();
                params.append('userId',this.userId);
                params.append('type',0);
                params.append('songId',this.id)
                setCollect(params).then( res => {
                    if(res.code == 1){
                        this.$store.commit('setIsActive',true);
                        this.notify('','success');
                    }else if(res.code == 2){
                        this.notify(res.msg,'warning');  
                    }else{
                        this.notify("",'error');
                    }
                }).catch(err => {
                    console.log(err);
                })
            }else{
                this.notify('','warning')
            }
        },
        //
        delCollect(userId,songId){
            if(this.isActive){
                delCollectByUserIdSongId(userId,songId)
                    .then( res => {
                        if(res){
                        this.$store.commit('setIsActive',false);
                        this.notify('','success');
                        }else{
                            this.notify("",'error');
                        }
                    }).catch(err => {
                        console.log(err);
                    })
            }
        },
        //
        handleSongList(userId,songId,songListId){
            let params = new URLSearchParams();
            params.append('songId',songId);
            params.append('songListId',songListId);
            setListSong(params)
        },
        //
        showPlayer(toggle){
            this.toggle = !toggle;
        }
    }
}
</script>
<style  lang="scss" scoped>
@import '../assets/css/play-bar.scss';
</style>



