<template>
    <transition name="slide-fade">
        <div class="the-aside" v-if="showAside">
            <h2 class="title"></h2>
            <ul class="menus">
                <li v-for="(item,index) in listOfSongs" :key="index" :class="{'is-play': id==item.id}"
                @click="toplay(item.id,item.url,item.pic,index,item.name,item.lyric)">
                    {{item.name.split('-')[0]}}
                    <!-- {{index}} -->
                </li>
            </ul>
        </div>
    </transition>
</template>
<script>
import {mapGetters} from 'vuex';
import {download, setCollect, getCollectOfUserId} from '../api/index';
export default {
    name: 'the-aside',
    computed:{
        ...mapGetters([
            'showAside', //
            'listOfSongs', //
            'id',//id
            'loginIn',//
            'userId',//id
            'isActive',//
        ])
    },
    mounted(){
        let _this = this;
        document.addEventListener('click',function(){
            _this.$store.commit('setShowAside',false)
        },true);
    },
    methods: {
        //
        notify(title, type){
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        attachImageUrl (srcUrl) {
            return srcUrl? "http://www.huole.xyz:9527" + srcUrl : '/img/consumerAvator/user.jpg';
        },
        //
        getSong(){
           if(!this.$route.query.keywords){
            this.$store.commit('setListOfSongs',[]);
               this.notify("",'warning');
           }else{
               songLikeName(this.$route.query.keywords).then( res => {
                   if(!res.length){
                    this.$store.commit('setListOfSongs',[]);
                        this.notify("",'warning');
                   }else{
                        this.$store.commit('setListOfSongs',res);
                   }
               }).catch(err => {
                   console.log(err)
               })
           }
        },
        //
        toplay: function(id,url,pic,index,name,lyric){
            this.$store.commit('setId',id);
            this.$store.commit('setUrl',"http://www.huole.xyz:9527"+url);
            this.$store.commit('setPicUrl',"http://www.huole.xyz:9527"+pic);
            this.$store.commit('setListIndex',index);
            this.$store.commit('setTitle',name.split('-')[0]);
            this.$store.commit('setArtist',name.split('-')[1]);
            this.$store.commit('setLyric',this.parseLyric(lyric));
            this.$store.commit('setIsActive',false);
            if(this.loginIn){
                getCollectOfUserId(this.userId)
                    .then( res => {
                        for(let item of res){
                            if(item.songId == id){
                                this.$store.commit('setIsActive',true);
                                break
                            }
                        }
                    })
            }
            // console.log(id)
        },
        //
        parseLyric(text){
            let lines = text.split("\n\n");   //
            
            if(lines.length < 2){
                lines = text.split("\r\n\r\n");
            }
            if(lines.length < 2){
                lines = text.split("\r\n");
            }
            if(lines.length < 2){
                lines = text.split("\n");
            }
            let pattern = /\[\d{2}:\d{2}.\d{2,3}\]/g;//
            let result = [];
            //
            if(!(/\[.+\]/.test(text))){
                return [[0,text]]
            }
            //
            while(!pattern.test(lines[0])){
                lines = lines.slice(1);
            }
            //+
            for(let item of lines){
                let time = item.match(pattern);//
                let value = item.replace(pattern,'');//
                for(let item1 of time){
                    let t = item1.slice(1,-1).split(":");//
                    if(value!=''){
                        result.push([parseInt(t[0],10) * 60 + parseFloat(t[1]), value]);
                    }
                }
            }
            //
            result.sort(function(a,b){
                return a[0] - b[0];
            });
        },
    }
}
</script>


<style  lang="scss" scoped>
@import '../assets/css/the-aside.scss';
</style>



